* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background: #A626D3;
}


.container {
    max-width: 570px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

@media(max-width:540px) {
.container {
    margin-top: 40px;
}
}

nav {
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    padding: 19px 20px;
    border-radius: 15px;
}

nav img {
    user-select: none;
}


main {
    display: flex;
    flex-direction: column;
}



.contant {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

@media(max-width:570px) {
    .contant {
        display: flex;
        /* align-items: center; */
        flex-direction: column;
    }
}

input {
    border: 1px solid #D5D4D8;
    border-radius: 5px;
    padding: 10px;
    margin: 0 5px;
}

@media(max-width:570px) {
    input {
        margin: 5px 0;
    }
}

button {
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    border-radius: 5px;
    color: #fff;
    padding: 10px 0;
    cursor: pointer;
    grid-column: 1 / -1;
    font-size: 17px;
    margin: 10px 5px;
}

.meme {
    position: relative;
    align-self: center;
}

.meme img {
    display: flex;
    width: 500px;
    border-radius: 5px;
}

@media(max-width:570px) {
    .meme img {
        width: 100%;
    }
}

.meme h2 {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: #A626D3;
    font-size: 2.4rem;
    text-align: center;
}

.meme-one {
    top: 20px;

}

.meme-two {
    bottom: 20px;
}